:root {
  --bg: #0b0b0b;
  --card: #131313;
  --text: #eaeaea;
  --muted: #b3b3b3;
  --accent: #ff2a2a;
}

.linkhub {
  min-height: 100vh;
  display: grid;
  place-items: center;
  background: radial-gradient(1000px 600px at 50% -10%, rgba(255,42,42,0.15), transparent 60%),
              radial-gradient(600px 600px at 90% 10%, rgba(255,42,42,0.08), transparent 60%),
              linear-gradient(135deg, #000000 0%, #1a1a1a 100%);
  padding: 2rem;
}

.card {
  width: 100%;
  max-width: 560px;
  background: var(--card);
  border: 1px solid #2a2a2a;
  border-radius: 20px;
  padding: 2rem;
  color: var(--text);
  box-shadow: 0 10px 40px rgba(0,0,0,0.4);
}

.avatar {
  display: grid;
  place-items: center;
  margin-bottom: 1.25rem;
}

.avatar img {
  width: 110px;
  height: 110px;
  border-radius: 999px;
  object-fit: cover;
  border: 3px solid var(--accent);
}

h1 {
  text-align: center;
  font-size: 1.6rem;
  margin: 0;
}

.tagline {
  text-align: center;
  color: var(--muted);
  margin: 0.5rem 0 1.5rem;
}

.links {
  display: grid;
  gap: 0.9rem;
}

.link {
  display: block;
  text-align: center;
  padding: 0.95rem 1.25rem;
  border-radius: 12px;
  text-decoration: none;
  color: var(--text);
  background: #1b1b1b;
  border: 1px solid #2a2a2a;
  transition: transform .15s ease, background .2s ease, border-color .2s ease;
}

.link .link-content {
  display: inline-flex;
  align-items: center;
  gap: 0.6rem;
}

.link:hover {
  transform: translateY(-2px);
  background: #222222;
  border-color: var(--accent);
}

.socials {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 1.25rem;
}

.socials a {
  color: var(--muted);
  font-size: 1.25rem;
}

@media (max-width: 480px) {
  .card { padding: 1.5rem; }
}


